<template>
  <v-container class="pa-7">
      <v-row>
          <v-col cols=12>
              <v-card>
                  <v-card-title>
                      Edit Todo
                  </v-card-title>
                  <form action="" @submit.prevent="save_todo">
                  <v-container class="pa-5">
                      <v-row>
                          <v-col>
                              <v-text-field
                                v-model="todo.name"
                                color="purple darken-2"
                                label="Name"
                                type="text"
                                required
                                >
                            </v-text-field>
                            <v-textarea
                                v-model="todo.description"
                                label="Description"
                                
                                >
                            </v-textarea>
                            <v-switch
                            v-model="todo.completed"
                            label="Completed"
                            ></v-switch>
                          </v-col>
                      </v-row>
                      <v-row>
                          <v-col cols=12>
                              <v-btn color="primary" type="submit" class="mr-5">Submit</v-btn>
                              <v-btn color="default" @click.prevent="$router.back()">Cancel</v-btn>
                          </v-col>
                      </v-row>
                  </v-container>
                  </form>
              </v-card>
          </v-col>
      </v-row>
  </v-container>
</template>

<script>
export default {
    mounted(){
        this.get_todo()
    },
    data: () => ({
        todo: {
            name:'',
            description: '',
            completed: false
        }
    }),
    methods: {
        get_todo(){
            this.$todos.select(this.$route.params._id).then(results => {
                this.todo = results
            })
        },
        save_todo(){
            this.$todos.update(this.$route.params._id, this.todo).then(() => {
                this.$router.push('/todos')
            })
        }
    }
}
</script>

